

.hero {
    width: 100vw;
    height: 543.1px;
    background-image: url('../images/cover.jpeg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display:flex;
    justify-content: flex-end;
    align-items: center;
    flex-direction: column;
    flex-wrap: nowrap;
    color: white;

    // @media all and (min-width: 768px) {
